body,
html {
  width: 100vw;
  height: 100vh;
  color: hsla(240, 10%, 15%, 1);
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  font-size: 20vw;
  margin: 0;
  text-shadow: 10px 10px 1px hsla(240, 100%, 30%, 0.1);
}

.input {
  max-width: 5rem;
  font-size: 2em;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  text-align: right;
  padding: 1rem;
  position: absolute;
  top: 1rem;
  right: 1rem;
  background-color: transparent;
  border: none;
  font-weight: bold;
  color: hsla(240, 10%, 15%, 1);
}

.input:focus {
  outline: none;
  background-color: white;
  border-radius: .25rem;
}

.container {
  flex: 1 1;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  background-color: hsla(240, 100%, 67%, 1);
}

.gradient {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  -webkit-animation: gradient 5s ease infinite;
  animation: gradient 5s ease infinite;
}

.buttons-container {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.Button_button__1fm_s {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: bold;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background: none;
  padding: 1rem 2.5rem;
  text-transform: uppercase;
  margin: 1rem;
  color: #2d2d2d;
  border-radius: 0.25rem;
  background-color: none;
  transition: all 250ms;
  cursor: pointer;
  background-color: hsla(240, 100%, 100%, 1);
  box-shadow: 5px 5px 1px hsla(240, 100%, 30%, 0.1);
}

.Button_button__1fm_s:hover {
  background-color: hsla(240, 100%, 30%, 0.1);
  color: hsla(240, 100%, 90%, 1);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  box-shadow: 6px 12px 12px hsla(240, 100%, 30%, 0.2);
}

.Button_button__1fm_s:active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  box-shadow: 5px 5px 1px hsla(240, 100%, 30%, 0.1);
  outline: none;
}

.Button_button__1fm_s:focus {
  outline: none;
}

